import uniPopup from '@/components/uni-popup/uni-popup.vue';
export default {
	data() {
		return {
			show: false,
			type: [],
			list: [],
			server: null
		};
	},
	components: { uniPopup },
	onLoad() {
		this.getServer();
		this.getServerType();
	},
	methods: {
		getServer() {
			this.$Fetch({
				url: 'serve_people/getDataById',
				debug: true,
				success: res => {
					console.log(res);
					if (res) this.server = res;
				}
			});
		},
		getServerType() {
			this.$Fetch({
				url: 'Qualifications/getData',
				data: { pageNum: 1, pageSize: 10 },
				debug: true,
				success: res => {
					console.log(res);
					let arr = res.lists;
					if (arr.length > 0) {
						this.list = arr;
					}
				}
			});
		},
		checkType(type) {
			this.type = type;
		},
		onSubmit(e) {
			console.log('提交数据：', e.detail.value);
			this.$Fetch({
				url: 'Order/add',
				data: e.detail.value,
				success: res => {}
			});
		}
	}
};
